:root {
  --basic-black: #000000;
  --basic-green: #00CA74;
  --basic-gray: #E1E1E1;
  --basic-red: #FB565A;
  --basic-yellow: #EFC849;
  --basic-white: #FFFFFF;

  --special-black: #283136;
  --special-gray: #666666;

  --decorated-black: #444444;
}

/* typography */

@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto.woff2") format("woff2"),
    url("../fonts/roboto.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/robotomedium.woff2") format("woff2"),
    url("../fonts/robotomedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/robotobold.woff2") format("woff2"),
    url("../fonts/robotobold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

/* tags */

body {
  width: 1160px;
  margin: 0 auto;
  padding: 0;

  color: var(--basic-black);
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;

  background-color: var(--basic-white);
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

/* common */

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.list-reset {
  margin: 0;
  padding: 0;

  list-style: none;
}

/* action-button */

.action-button {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  width: 160px;
  padding: 17px 0 15px;

  color: var(--basic-white);
  font-weight: 500;
  text-align: center;
  text-transform: uppercase;

  background-color: var(--basic-red);
  border: none;
  border-radius: 3px;
}

.action-button--green {
  background-color: var(--basic-green);
}

.action-button--yellow {
  background-color: var(--basic-yellow);
}

/* main-header */

.main-header__navigation a {
  color: inherit;
  font-weight: 500;
  line-height: 30px;
  text-transform: uppercase;
}

/* principles */

.principles__slide-title {
  font-weight: 500;
  font-size: 55px;
  line-height: 55px;
}

.principles__slide-description {
  color: var(--special-black);
}

.principles__slide-button {
  width: 240px;
}

/* services */

.services__servive-title {
  font-weight: 700;
  text-transform: uppercase;
}

.services__servive-description {
  color: var(--special-black);
}

/* about */

.about__title {
  font-weight: 500;
  font-size: 45px;
  line-height: 45px;
}

.about__description {
  color: var(--special-black);
}

.about__services-title {
  color: var(--special-black);
  font-weight: 700;
  text-transform: uppercase;
}

.about__services-list li {
  color: var(--special-black);
}

.about__properties-title {
  color: var(--special-black);
  font-weight: 700;
  text-transform: uppercase;
}

.about__properties-list dt {
  color: var(--special-black);
  line-height: 18px;
}

.about__properties-list dd {
  font-weight: 700;
  font-size: 45px;
  line-height: 64px;
}

.about__properties-list span {
  font-size: 26px;
  line-height: 40px;
  vertical-align: text-top;
}

/* map */

.map__description {
  width: 319px;
  padding: 49px 50px 47px;

  background-color: var(--basic-white);
}

.map__description-title {
  font-weight: 700;
  font-size: 18px;
  line-height: 30px;
  text-transform: uppercase;
}

.map__description-address,
.map__description-telephone {
  color: var(--special-gray);
  line-height: 18px;
}

.map__description-button {
  width: 219px;
}

/* main-footer */

.main-footer__link {
  display: flex;
  justify-content: center;
  width: 81px;
  height: 81px;

  background-color: var(--basic-gray);
  border-radius: 50%;
}

.main-footer__promotion-title {
  font-weight: 700;
  font-size: 36px;
  line-height: 36px;
}

.main-footer__promotion-description {
  color: var(--decorated-black);
  line-height: 22px;
}

/* popup */

.popup__title {
  font-weight: 700;
  font-size: 45px;
  line-height: 53px;
}

.popup__field label {
  font-weight: bold;
  line-height: 18px;
}

.popup__button {
  width: 259px;
}
